import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Layout from '../../components/Layout/Layout';
import toast from 'react-hot-toast';

const Register = () => {

    let navigate = useNavigate();

    // Initialize form data state
    const [formData, setFormData] = useState({ firstName: '', lastName: '', phone: '', email: '', password: '' });

    // Handle input changes and update form data state
    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setFormData({ ...formData, [name]: value });
    };

    // Base URL for API endpoint
    const serverUrl = process.env.REACT_APP_API;

    // Handle form submission
    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            // Make a POST request to the server
            const response = await fetch(`${serverUrl}/users/signup`, {
                method: "POST",
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(formData)
            });

            // Parse the response as JSON
            const responseData = await response.json();

            // Update success or error messages based on server response
            if (responseData.success) {
                toast.success(responseData.message);
                setTimeout(() => navigate('/login/'), 3000); // Pass the navigate function as a callback
            } else {
                toast.error(responseData.message || 'An error occurred.');
            }
        } catch (error) {
            toast.error('An error occurred. Please try again later');
        }
    };

    return (
        <Layout>
            <div className="section">
                <div className="container">
                    <div className="row">
                        <div className="col-md-7">
                            <div className="billing-details">
                                <div className="section-title">
                                    <h3 className="title">Register</h3>
                                </div>
                                <form name="rf" onSubmit={handleSubmit}>
                                    <div className="form-group">
                                        <input type="text" name="firstName" placeholder="First Name" className="input" required value={formData.firstName} onChange={handleInputChange} />
                                    </div>
                                    <div className="form-group">
                                        <input type="text" name="lastName" placeholder="Last Name" className="input" required value={formData.lastName} onChange={handleInputChange} />
                                    </div>
                                    <div className="form-group">
                                        <input type="text" name="phone" placeholder="Phone Number" className="input" required value={formData.phone} onChange={handleInputChange} /> {/* Fixed typo */}
                                    </div>
                                    <div className="form-group">
                                        <input type="email" name="email" placeholder="Email Id" className="input" required value={formData.email} onChange={handleInputChange} />
                                    </div>
                                    <div className="form-group">
                                        <input type="password" name="password" placeholder="Your Password" className="input" required value={formData.password} onChange={handleInputChange} />
                                    </div>
                                    <button type="submit" className="primary-btn order-submit">Register</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}

export default Register;
