import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Layout from '../../components/Layout/Layout';
import toast from 'react-hot-toast';
import { useAuth } from '../../context/auth';

const Login = () => {
    const [auth, setAuth] = useAuth()
    // React Router hook for navigation
    const navigate = useNavigate();

    const [phone, setPhone] = useState('');
    const [password, setPassword] = useState('');

    const serverUrl = "http://localhost:9000";

    // Function to handle form submission
    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const response = await fetch(`${serverUrl}/users/signin`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ phone, password }) // Prepare data for submission
            });

            const result = await response.json(); // Parse the response
            if (result.success) {
                setAuth({
                    ...auth,
                    user: result.user,
                    token: result.token
                });
                // Save token and user data to local storage
                localStorage.setItem("auth", JSON.stringify(result));
                navigate("/");
            } else {
                toast.error(result.message);
            }
        } catch (error) {
            console.error('Login error:', error);
            toast.error('An error occurred during login.');
        }
    };
    return (
        <Layout>
            <div className="section">
                <div className="container">
                    <div className="row">
                        <div className="col-md-7">
                            <div className="billing-details">
                                <div className="section-title">
                                    <h3 className="title">Login</h3>
                                </div>
                                <form name="rf" onSubmit={handleSubmit}>
                                    <div className="form-group">
                                        <input type="text" name="phone" placeholder="Phone Number" className="input" required value={phone} onChange={(e) => setPhone(e.target.value)} />
                                    </div>
                                    <div className="form-group">
                                        <input type="password" name="password" placeholder="Password" className="input" required value={password} onChange={(e) => setPassword(e.target.value)} />
                                    </div>
                                    <button type="submit" className="primary-btn order-submit">Login</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default Login