import React from 'react'
import { Link, NavLink } from "react-router-dom";
import { useAuth } from '../../context/auth';

const Header = () => {
    const [auth, setAuth] = useAuth();
    const handelLogout = () => {
        setAuth({
            ...auth,
            user: null,
            token: ""
        })
        localStorage.removeItem('auth');
    }
    return (
        <>
            <header>
                <div id="top-header">
                    <div className="container">
                        <ul className="header-links pull-left">
                            <li><Link to="/"><i className="fa fa-phone" /> +021-95-51-84</Link></li>
                            <li><Link to="/"><i className="fa fa-envelope-o" /> email@email.com</Link></li>
                            <li><Link to="/"><i className="fa fa-map-marker" /> 1734 Stonecoal Road</Link></li>
                        </ul>
                        <ul className="header-links pull-right">
                            {
                                !auth.user ? (
                                    <>
                                        <li><Link to="/login"><i className="fa fa-user-o" /> Login</Link></li>
                                        <li><Link to="/register"><i className="fa fa-user-o" /> Register</Link></li>
                                    </>) : (<>
                                        <li><Link to={`dashboard/${auth?.user?.role === 1 ? "admin" : "user"}`}>
                                            <i className="fa fa-user-o" /> My Account
                                        </Link></li>
                                        <li><Link onClick={handelLogout} to="/login"><i className="fa fa-user-o" /> Logout</Link></li>
                                    </>)
                            }
                        </ul>
                    </div>
                </div>
                <div id="header">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="header-logo">
                                    <Link to="/" className="logo">
                                        <img src="http://localhost:3000/img/logo.png" alt="df" />
                                    </Link>
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="header-search">
                                    <form>
                                        <input className="input" placeholder="Search here" />
                                        <button className="search-btn">Search</button>
                                    </form>
                                </div>
                            </div>
                            <div className="col-md-3 clearfix">
                                <div className="header-ctn">
                                    <div>
                                        <Link to="/">
                                            <i className="fa fa-heart-o" />
                                            <span>Your Wishlist</span>
                                            <div className="qty">2</div>
                                        </Link>
                                    </div>
                                    <div className="dropdown">
                                        <a className="dropdown-toggle" data-toggle="dropdown" aria-expanded="true">
                                            <i className="fa fa-shopping-cart" />
                                            <span>Your Cart</span>
                                            <div className="qty">3</div>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
            <nav id="navigation">
                <div className="container">
                    <div id="responsive-nav">
                        <ul className="main-nav nav navbar-nav">
                            <li className="active"><NavLink to="/">Home</NavLink></li>
                            <li><NavLink to="/about">About Us</NavLink></li>
                            <li><NavLink to="/">Categories</NavLink></li>
                            <li><NavLink to="/">Laptops</NavLink></li>
                            <li><NavLink to="/">Smartphones</NavLink></li>
                            <li><NavLink to="/">Cameras</NavLink></li>
                            <li><NavLink to="/contact">Contact Us</NavLink></li>
                        </ul>
                    </div>
                </div>
            </nav>

        </>
    )
}

export default Header